let STORAGE_KEY = 'footList';
export default {
  fetch(type) {
    return JSON.parse(window.localStorage.getItem(STORAGE_KEY) || '[]');
  },
  save(item, type) {
    window.localStorage.setItem(STORAGE_KEY, JSON.stringify(item));
  },
  reset() {
    window.localStorage.clear();
  }
};



